<template>
  <div>
    <el-row style="margin-bottom:20px;">
      <el-card shadow="always">
        <span class="content-title">咨询管理</span>
        <el-button
          type="success"
          @click="openAddDialog()"
        >添加</el-button>
      </el-card>
    </el-row>
    <el-table
      border
      :data="tableData"
    >
      <el-table-column
        prop="index"
        label="序号"
        align="center"
        width="80"
      >
      </el-table-column>
      <el-table-column
        prop="company_name"
        label="公司名称"
        align="center"
      >
      </el-table-column>
      <el-table-column
        prop="company_address"
        label="公司地址"
        align="center"
      >
      </el-table-column>
      <el-table-column
        prop="name"
        label="联系人姓名"
        align="center"
        width="120"
      >
      </el-table-column>
      <el-table-column
        prop="mobile"
        label="联系人号码"
        align="center"
        width="130"
      >
      </el-table-column>
      <el-table-column
        prop="mail"
        label="联系人邮箱"
        align="center"
        width="180"
      >
      </el-table-column>
      <el-table-column
        prop="create_time"
        label="创建时间"
        align="center"
        width="200"
      >
      </el-table-column>
      <el-table-column
        label="操作"
        align="center"
        width="100"
      >
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="primary"
            @click="handleEdit(scope.$index, scope.row)"
          >详情</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="block pager">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-sizes="[15, 20, 30]"
        :page-size="page_size"
        layout="sizes, prev, pager, next"
        :total="total"
      >
      </el-pagination>
    </div>
    <el-dialog
      title="添加"
      :visible.sync="dialogVisible"
      width="50%"
      :append-to-body='true'
      :before-close="handleClose"
      :closeOnClickModal='false'
    >
      <div class="el-dialog-counsel">
        <el-form
          label-position="top"
          :rules="rules"
          ref="formLabelAlign"
          label-width="80px"
          :model="formLabelAlign"
        >
          <el-form-item
            label="公司名称"
            prop="company_name"
          >
            <el-input v-model="formLabelAlign.company_name" :disabled="formLabelAlign.hasOwnProperty('con_id')"></el-input>
          </el-form-item>
          <el-form-item
            label="公司地址"
            prop="company_address"
          >
            <el-input v-model="formLabelAlign.company_address" :disabled="formLabelAlign.hasOwnProperty('con_id')"></el-input>
          </el-form-item>
          <el-form-item
            label="公司简介"
            prop="company_summary"
          >
            <el-input
              type="textarea"
              v-model="formLabelAlign.company_summary"
              :autosize="{ minRows: 10, maxRows: 33}"
              placeholder="请输入内容"
              :disabled="formLabelAlign.hasOwnProperty('con_id')"
            >
            </el-input>
          </el-form-item>
          <el-form-item
            label="行业"
            prop="industry"
          >
            <el-input v-model="formLabelAlign.industry" :disabled="formLabelAlign.hasOwnProperty('con_id')"></el-input>
          </el-form-item>
          <el-form-item
            label="联系人姓名"
            prop="name"
          >
            <el-input v-model="formLabelAlign.name" :disabled="formLabelAlign.hasOwnProperty('con_id')"></el-input>
          </el-form-item>
          <el-form-item
            label="职业"
            prop="job"
          >
            <el-input v-model="formLabelAlign.job" :disabled="formLabelAlign.hasOwnProperty('con_id')"></el-input>
          </el-form-item>
          <el-form-item
            label="联系人号码"
            prop="mobile"
          >
            <el-input
              type="number"
              v-model="formLabelAlign.mobile"
              :disabled="formLabelAlign.hasOwnProperty('con_id')"
            ></el-input>
          </el-form-item>
          <el-form-item
            label="联系人邮箱"
            prop="mail"
          >
            <el-input v-model="formLabelAlign.mail" :disabled="formLabelAlign.hasOwnProperty('con_id')"></el-input>
          </el-form-item>
          <el-form-item label="兴趣意向" prop="business">
            <el-input v-model="formLabelAlign.business" :disabled="formLabelAlign.hasOwnProperty('con_id')"></el-input>
          </el-form-item>
          <el-form-item
            label="页面信息"
            prop="page_info"
          >
            <el-input v-model="formLabelAlign.page_info" :disabled="formLabelAlign.hasOwnProperty('con_id')"></el-input>
          </el-form-item>
        </el-form>
      </div>
      <span
        slot="footer"
        class="dialog-footer"
        v-if="!formLabelAlign.hasOwnProperty('con_id')"
      >
        <el-button @click="handleClose">取 消</el-button>
        <el-button
          type="primary"
          @click="submitDialog('formLabelAlign')"
        >提 交</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import axios from "axios";
import { utcTimeChange } from "@/utils";
export default {
  data() {
    var validateMobile = (rule, value, callback) => {
      var reg = 11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/;
      if (value === "") {
        callback(new Error("请输入手机号"));
      } else if (!reg.test(this.formLabelAlign.mobile)) {
        callback(new Error("手机号格式不正确"));
      } else {
        callback();
      }
    };
    var validateEmail = (rule, value, callback) => {
      var regEmail = new RegExp(
        "^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$"
      );
      if (value != "" && !regEmail.test(this.formLabelAlign.mail)) {
        callback(new Error("邮箱格式不正确"));
      } else {
        callback();
      }
    };
    return {
      dictionary_list: "[]",
      selectClass: "el-icon-arrow-down",
      content_show: false,
      //表格
      tableData: [],
      currentPage: 1,
      page_size: 15,
      total: 0,
      //弹出框
      dialogVisible: false,
      //表单
      formLabelAlign: {
        name: "",
        mobile: "",
        mail: "",
        company_name: "",
        company_address: "",
        company_summary: "",
        industry: "",
        job:"",
        business: "",
        page_info:"",
      },
      rules: {
        name: [{ required: true, message: "请输入姓名", trigger: "blur" }],
        mobile: [
          { required: true, message: "请输入手机号", trigger: "blur" },
          { validator: validateMobile, trigger: "blur" }
        ],
        company_name: [
          { required: true, message: "请输入企业名称", trigger: "blur" }
        ],
        industry: [{ required: true, message: "请输入行业" }],
        mail: [{ validator: validateEmail, trigger: "blur" }]
      }
    };
  },
  mounted() {
    //this.getDictionary();
    this.getInfoList("1", "15");
  },
  methods: {
    getDictionary() {
      var that = this;
      var params = { code: "business" };
      axios.post("core/dictionary/list", params).then(
        function(response) {
          console.log(response)
          var code = response.code;
          if (0 == code) {
            that.dictionary_list = response.data;
          } else {
          }
        },
        function(error) {}
      );
    },
    handleSizeChange(val) {
      this.getInfoList(this.currentPage, val);
    },
    handleCurrentChange(val) {
      this.getInfoList(val, this.page_size);
    },
    //详情
    handleEdit(index, row) {
      this.dialogVisible = true;
      this.formLabelAlign = {
        con_id: row.con_id,
        name: row.name,
        mobile: row.mobile,
        mail: row.mail,
        company_name: row.company_name,
        company_address: row.company_address,
        company_summary: row.company_summary,
        job: row.job,
        industry: row.industry,
        business: row.business,
        page_info: row.page_info
      };
    },
    handleClose() {
      if(!this.formLabelAlign.hasOwnProperty('con_id')){
        var that = this;
        this.$confirm('是否保存？',{
          showClose:false
        })
        .then(() => {
          that.submitInfo('formLabelAlign');
        })
        .catch(() => {this.dialogVisible = false});
      }else{
        this.dialogVisible = false;
      }
    },
    openAddDialog() {
      this.formLabelAlign = {
        name: "",
        mobile: "",
        mail: "",
        company_name: "",
        company_address: "",
        company_summary: "",
        industry: "",
        job:"",
        business: "",
        page_info:"",
      };
      this.dialogVisible = true;
    },
    submitDialog(formType) {
      var that = this;
      this.$refs[formType].validate(valid => {
        if (valid) {
          var method = "";
          var info = "";
          if (that.formLabelAlign.hasOwnProperty("con_id")) {
            method = "content/consultative/update";
            info = "修改成功!";
          } else {
            method = "content/consultative/create";
            info = "添加成功!";
          }
          that.submitInfo(method, that.formLabelAlign, info);
        } else {
          return false;
        }
      });
    },
    cancleDialog() {
      this.dialogVisible = false;
    },
    getInfoList(page_index, page_size) {
      var params = { page_index: page_index, page_size: page_size };
      var that = this;
      axios.get("content/consultative/pageList", { params }).then(
        function(response) {
          var code = response.code;
          if (0 == code) {
            var array = [];
            var rdata = response.data;
            var count = rdata.count;
            var page_index = rdata.page_index;
            var page_size = rdata.page_size;
            that.currentPage = page_index;
            that.page_size = page_size;
            that.total = count;
            var des_num = (page_index - 1) * page_size;
            var json = rdata.rows;
            for (var i = 0; i < json.length; i++) {
              json[i].index = des_num + i + 1;
              json[i].create_time = utcTimeChange(json[i].create_time);
              if(json[i].business){
                json[i].business = (json[i].business).join(",");
              }
              array.push(json[i]);
            }
            that.tableData = array;
          } else {
            //console.log(code);
          }
        },
        function(error) {
          //console.log(error);
        }
      );
    },
    submitInfo(method, params, info) {
      var that = this;
      if(params.business){
        params.business = params.business.split(",");
      }else{
        params.business = [];
      }
      axios.post(method, params).then(
        function(response) {
          var code = response.code;
          if (0 == code) {
            that.$message({
              type: "success",
              message: info
            });
            that.dialogVisible = false;
            that.getInfoList(that.currentPage, that.page_size);
          } else {
            that.$message({
              type: "success",
              message: "操作失败:" + response.message
            });
          }
        },
        function(error) {}
      );
    }
  }
};
</script>

<style scoped>
.content-title {
  margin-right: 20px;
}
.advanced-box {
  padding: 20px 10px;
  margin-top: 15px;
  background-color: #f7f7f7;
}
.pager {
  float: right;
  padding: 10px 0;
}
.el-dialog__header {
  border-bottom: 1px solid #ccc;
}
.el-dialog-counsel {
  height: 530px !important;
  padding-right: 15px;
  overflow-y: scroll;
}
.el-dialog__footer {
  border-top: 1px solid #ccc;
}
.el-form-item__label {
  padding-bottom: 0 !important;
}
</style>


